@import url(https://fonts.googleapis.com/css2?family=Antonio:wght@500&family=Spartan:wght@400;700&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
}

#root {
    background-color: #070724;
    font-family: "Antonio", "Spartan", sans-serif;
    background-image: url(/static/media/background-stars.f6233a6a.svg);
    min-height: 100vh;
    width: 100%;
}

header {
    display: -webkit-flex;
    display: flex;
    height: 8%;
    width: 100%;
    border-bottom: 1px solid #ffffff46;
}

main {
    display: -webkit-flex;
    display: flex;
    /* height: 92%; */
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin: 8vh 12% 0% 12%;
    height: auto;
}

.header-child1 {
    margin-left: 4vw;
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
}

.header-child1 > h2 {
    margin: 3% 3%;
    color: #ffffff;
    font-size: 1.75em;
    font-weight: 500;
    letter-spacing: -1.05px;
}

.header-child2 {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    width: 50%;
}

.header-child2 > button {
    background-color: transparent;
    font-family: "Spartan", sans-serif;
    font-weight: 700;
    color: #ffffff;
    font-size: 0.6875em;
    letter-spacing: 1px;
    border: none;
    outline: none;
}

.header-child2 > button:hover {
    cursor: pointer;
    outline: none;
    height: 100%;
}

.buttons-planet-mercury:focus {
    background-color: #419ebb;
}

.btn-mercury:hover {
    border-top: solid #419ebb;
}

.btn-venus:hover {
    border-top: solid #eda249;
}

.btn-earth:hover {
    border-top: solid #6d2ed5;
}

.btn-mars:hover {
    border-top: solid #d14c32;
}

.btn-jupiter:hover {
    border-top: solid #d83a34;
}

.btn-saturn:hover {
    border-top: solid #cd5120;
}

.btn-uranus:hover {
    border-top: solid #1ec1a2;
}

.btn-neptune:hover {
    border-top: solid #2d68f0;
}

.main-content-row1 {
    display: -webkit-flex;
    display: flex;
    height: auto;
    width: auto;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.main-planet-description {
    width: 32%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.main-planet-img {
    width: 40vw;
    height: 50vh;
    -webkit-align-self: center;
            align-self: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
}

.planet {
    max-width: 40vw;
    max-height: 50vh;
    -webkit-align-self: center;
            align-self: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
}

.main-planet-img-mobile {
    display: none;
}

.buttons-planet-unique {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.main-planet-description h1 {
    font-family: "Antonio", sans-serif;
    font-weight: 500;
    color: #ffffff;
    font-size: 5em;
}

.overview-content {
    font-family: "Spartan", sans-serif;
    color: #ffffff;
    font-size: 0.875em;
    line-height: 1.5625em;
    margin: 5% 0%;
}

.overview-source {
    margin-bottom: 4%;
    font-family: "Spartan", sans-serif;
    font-weight: 400;
    font-size: 0.875em;
    color: #ffffff73;
}

.overview-source > a {
    font-weight: 700;
    color: #ffffff83;
}

.buttons-planet-unique > button,
.description-button-movile {
    border: 1px solid #ffffff46;
    background: transparent;
    height: 5em;
    margin: 2% 0%;
    padding: 1em;
    color: #ffffff;
    text-align: start;
    font-family: "Spartan", sans-serif;
    font-weight: 700;
    font-size: 0.75em;
    letter-spacing: 0.15625em;
}

@media screen and (min-width: 740px) {
    .description-button-movile {
        display: none;
    }
    .header-child2-mobile {
        display: none;
    }
}

.buttons-planet-unique > button:hover {
    background-color: #38384f;
    cursor: pointer;
}

.buttons-numbers {
    margin: 0% 6%;
    color: #ffffff83;
}

.react-arrow {
    height: 0.875em;
    width: 0.875em;
    position: relative;
    top: 0.1875em;
    left: 0.3125em;
}

.main-content-row2 {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
    margin-top: 8vh;
    margin-bottom: 4vh;
}

.main-content-row2 > div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 14vh;
    width: 20%;
    border: 1px solid #ffffff46;
    padding: 1.5em 2.5em 1.5em 1em;
}

.main-content-row2 p {
    font-family: "Spartan", sans-serif;
    font-weight: 700;
    font-size: 0.7125em;
    color: #ffffff83;
    letter-spacing: 1px;
    margin-bottom: 4%;
}

.main-content-row2 h2 {
    font-family: "Antonio", sans-serif;
    font-weight: 500;
    font-size: 2.2em;
    color: #ffffff;
    letter-spacing: -1.5px;
    white-space: nowrap;
}

.geology {
    position: absolute;
    height: 18%;
    margin-top: 0%;
    -webkit-align-self: flex-end;
            align-self: flex-end;
}

